    color: #337ab7;
        font-size:16px;
    background-color: #ffffff;
    border: 1px solid #dddddd;
    margin-left: -1px;
}
.custom-pagination > div a {
    position: relative;
    float: left;
    padding: 6px 12px;
    line-height: 1.42857143;
    font-size:16px;
    text-decoration: none;
    color: #337ab7;
    background-color: #ffffff;
    border: 1px solid #dddddd;
    margin-left: -1px;
}

.custom-pagination > div a:hover, .custom-pagination > div.current:hover,
.custom-pagination > div a:focus, .custom-pagination > div.current:focus {
    color: #23527c;
