}

/* Individual Controls
 ------------------------------------------------------------- */

.radio {
  display: inline-block;
  padding-right: 20px;
  cursor: pointer;
}
.invoer-radio {
  left: 10px;
}
/*
.radio:hover .inner {
  -webkit-transform: scale(0.5);
  -ms-transform: scale(0.5);
  transform: scale(0.5);
  opacity: .5;
}
*/
.radio input {
  width: 1px;
  height: 1px;
  opacity: 0;
}

.radio input:checked + .outer .inner {
  -webkit-transform: scale(1);
  -ms-transform: scale(1);
  transform: scale(1);
  opacity: 1;
}

.radio input:checked + .outer { border: 1px solid #aaa; }
/*
.radio input:focus + .outer .inner {
  -webkit-transform: scale(1);
  -ms-transform: scale(1);
  transform: scale(1);
  opacity: 1;
  background-color: #e67012;
}
*/
.radio .outer {
  position: absolute;
  /*left: 10px;*/
  width: 17px;
  height: 17px;
  display: block;
  float: left;
  margin: 2px 5px 0px 0px;
  border: 1px solid #aaa;
  border-radius: 50%;
  background-color: #fff;
}

.radio .inner {
  -webkit-transition: all 0.15s ease-in-out;
  transition: all 0.15s ease-in-out;
  width: 11px;
  height: 11px;
  -webkit-transform: scale(0);
  -ms-transform: scale(0);
  transform: scale(0);
  display: block;
  margin: 2px;
  border-radius: 50%;
  background-color: #5cb85c;
  opacity: 0;
}

/* basic css */
input[type=checkbox]   { display:none; }
/*label, p               { padding-left:50px; }*/
.checkbox_label         {
  position:relative;
  /*padding-top: 10px !important;*/
  margin-top: 10px;
  margin-bottom: 10px;
  min-height: 10px;
  min-width: 100%;
  width: 90%;
}

/* checkbox css */
label                  { cursor:pointer; }
label .checkbox             { display:block; position:absolute; left:0;
  -webkit-transition-duration:0.3s; -moz-transition-duration:0.3s; transition-duration:0.3s;
}
label .circle          {
  background:#FFF;
  left: -30px;
  top: -30px;
  height: 80px;
  width: 80px;
  z-index: -1;
  border-radius:50%; -moz-border-radius:50%; -webkit-border-radius:50%;
}
label .box     {
  /*border:2px solid #000;*/
  margin-top: 0px !important;
  border: 1px solid #aaa;
  border-radius: 10%;
  height: 14px;
  width: 14px;
  top: 3px;
  left: 10px;
  z-index: 888;
  -webkit-transition-delay: 0.15s;
  -moz-transition-delay: 0.15s;
  transition-delay: 0.15s;
}
label .check         {
  margin-top: 0px !important;
  top: 1px;
  left: 14px;
  width: 7px;
  height: 13px;
  border: 2px solid #5cb85c;
  border-top: none;
  border-left: none;
  opacity: 0;
  z-index: 888;
  -webkit-transform: scale(0) rotate(90deg); scale(0) -moz-transform:rotate(90deg); scale(0) transform:rotate(90deg);
  -webkit-transition-delay:0.15s; -moz-transition-delay:0.15s; transition-delay:0.15s;
}
label .checkbox-text {
    padding-left: 35px;
}
label .radio-text {
    padding-left: 25px;
}

/* when checkbox is checked */
label .circle {
  /*
  -webkit-animation   :growCircle 0.3s ease;
  -moz-animation      :growCircle 0.3s ease;
  animation           :growCircle 0.3s ease;
  */
}
input[type=checkbox]:checked ~ label .box {
  opacity:0;
  -webkit-transform   :scale(0) rotate(-90deg);
  -moz-transform       :scale(0) rotate(-90deg);
  transform           :scale(0) rotate(-90deg);
}
input[type=checkbox]:checked ~ label .check {
  opacity:1;
  -webkit-transform   :scale(1) rotate(45deg);
  -moz-transform      :scale(1) rotate(45deg);
  transform           :scale(1) rotate(45deg);
}

/* bubble animation */

@-webkit-keyframes growCircle {
  0%, 100%   { -webkit-transform:scale(0); }
  70%        { background:#c0ffc1; -webkit-transform:scale(1.25); }
}
@-moz-keyframes growCircle {
  0%, 100%   { -moz-transform:scale(0); }
