.linkbutton {
    padding: 6px 12px;
    color: #fff;
    /* text-shadow: 0px 1px 0 rgba(0,0,0,0.25); */
    border-radius: 4px;
    background: #ff7640;
    /* cursor: pointer; */
    /* box-shadow: inset 0 0 2px rgba(256,256,256,0.75); */
    -moz-box-shadow: inset 0 0 2px rgba(256,256,256,0.75);
    /* -webkit-box-shadow: inset 0 0 2px rgba(256,256,256,0.75); */
    transition: all 0.5s;
    -moz-transition: all 0.5s;
    /* -webkit-transition: all 0.5s; */
    -o-transition: all 0.5s;
    -ms-transition: all 0.5s;
    text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.2);
    -webkit-box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.15), 0 1px 1px rgba(0, 0, 0, 0.075);
    box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.15), 0 1px 1px rgba(0, 0, 0, 0.075);
}
.linkbutton:hover, .linkbutton:hover {
    color: #fff;
    background: #171717;
    box-shadow: inset 0 1px 3px rgba(0,0,0,0.5);
    -moz-box-shadow: inset 0 1px 3px rgba(0,0,0,0.5);
    -webkit-box-shadow: inset 0 1px 3px rgba(0,0,0,0.5);
}
a.linkbutton:link, a.linkbutton:visited {
    color: #fff;
    text-decoration: none;
    background: #ff7640;
}
a.linkbutton:hover, a.linkbutton:focus {
    color: #fff;
    text-decoration: none;
    background-color: #265a88;
}

/*  BEGIN START/STOP TICKETS  */
.timer-button {
    height: 20px;
    text-align: center;
    color: #FFFFFF;
    font-size: 0.8em;
    cursor: pointer;
    overflow: hidden;
    border-radius: 3px;
}

.timer-button.start {
    background-color: #4c9ce1;
    /*background-color: #4CAF50;*/
}
.timer-button.start:hover {
    background-color: #42A5F5;
    /*background-color: #66BB6A;*/
}

.timer-button.stop {
    /*background-color: #2196F3;*/
    background-color: #4CAF50;
}
.timer-button.stop:hover {
    background-color: #66BB6A;
    /*background-color: #42A5F5;*/
}

.timer-button > .button {
    position: relative;
    float: left;
    width: 51px;
    height: 100%;
    padding-left: 10px;
    padding-top: 0px;
    padding-right: 10px;
    border-right: 1px solid #FFFFFF;
}

.timer-button .right-element {
    text-align: right;
    width:80px;
    float: right;
    line-height: 1.8em;
    padding-left: 10px;
    padding-top: 0px;
    padding-right: 10px;
}

.timer-button > .timer {
    -webkit-transition: 200ms ease-out;
    -moz-transition: 200ms ease-out;
    -o-transition: 200ms ease-out;
    transition: 200ms ease-out;
}

.timer-button.start > .timer {
    position: relative;
    top: -24px;
}
.timer-button.stop > .timer {
    position: relative;
    top: -1px;
}
